@import "reset";
@import "typography";

.banner {
	padding: 175px 30px 48px;
	&--gray {
		background-color: #eff4f4; }
	&__title {
		margin-bottom: 32px; }
	&__text {
		max-width: 650px;
		margin-bottom: 55px; }
	&__button {
		margin-right: 20px;
		margin-bottom: 20px; } }

.easy-setup {
	padding: 67px 30px 17px;
	&__title {
		margin-bottom: 16px; }
	&__text {
		margin-bottom: 15px; }
	&__items {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		counter-reset: item;
		margin: 45px 0; }
	&__item {
		position: relative;
		max-width: 25%;
		flex-basis: 25%;
		margin-bottom: 22px;
		@media (max-width: 800px) and (orientation:portrait) {
			margin-bottom: 35px; }

		&::before {
			position: absolute;
			top: 10px;
			left: -50px;
			font-weight: 700;
			font-size: 150px;
			line-height: 1;
			color: #e1e2e9;
			counter-increment: item;
			content: counter(item);
			@media (max-width: 800px) and (orientation:portrait) {
				top: -15%;
				left: 35%; }
			@media (max-width: 800px) and (orientation:landscape) {
				top: -15%;
				left: -11%; } }
		&:nth-of-type(odd)::before {
			@media (max-width: 800px) and (orientation:portrait) {
				left: 65%; } }


		@media (max-width: 420px) {
			max-width: 100%;
			flex-basis: 100%; }

		@media (min-width: 420px) and (max-width: 800px) {
			max-width: 50%;
			flex-basis: 50%; } }

	&__link {
		font-weight: 400;
		font-size: 18px;
		line-height: 1.5;
		text-decoration: underline;
		letter-spacing: 0.45px;
		color: #0da088; }
	&__icon {
		position: relative;
		display: flex;
		flex-direction: column;
		&::before {
			margin-bottom: 4px;
			width: 100%;
			height: 160px;
			content: "";
			background-size: 50px;
			background-repeat: no-repeat;
			background-position: left 0 top 77px;
			@media (max-width: 800px) {
				height: 80px;
				background-position: left 0 center; } }

		&--notepad::before {
			background-image: url("../img/notepad.png"); }
		&--window::before {
			background-image: url("../img/window.png"); } } }

.faq-section {
	padding: 80px 30px 20px;
	&--gray {
		background-color: #eff4f4; }
	&__title {
		margin-bottom: 25px; }
	&__faq {
		margin: 62px 0 22px; } }

.section {
	padding: 80px 30px 20px;
	&--gray {
		background-color: #eff4f4; }
	&__text {
		margin-bottom: 15px;
		max-width: 768px; }
	&__banner {
		padding-top: 175px; }
	&__buttons {
		margin-top: 55px;
		margin-bottom: 45px;
		& > * {
			margin-right: 20px; } } }


.faq {
	&__list {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		margin: 0;
		padding: 0;
		list-style: none; }
	&__item {
		position: relative;
		max-width: 815px;
		width: 100%;
		padding-left: 40px;
		&::before {
			position: absolute;
			top: 14px;
			left: 1px;
			display: block;
			width: 8px;
			height: 8px;
			content: "";
			background-color: #3b4269;
			border-radius: 50%; } }
	&__title {
		display: block;
		margin-bottom: 10px;
		font-weight: 400;
		font-size: 24px;
		line-height: 1.5;
		letter-spacing: 0;
		text-decoration: underline;
		color: #0da088; }
	&__desc {
		margin: 10px 0 45px;
		font-weight: 400;
		font-size: 16px;
		line-height: 1.25;
		letter-spacing: 0.4px;
		color: #74757b; } }

.finish-setup {
	padding: 65px 30px 50px;
	&--gray {
		background-color: #eff4f4; }
	&__title {
		margin-bottom: 17px; }
	&__text {
		margin-bottom: 15px; }
	&__steps {
		margin: 60px 0 75px;
		padding: 0;
		display: block;
		list-style: none;
		counter-reset: step; }
	&__button {
		min-width: 266px;
		margin-right: 20px;
		margin-bottom: 20px; }
	&__step {
		display: flex;
		align-items: center;
		min-height: 75px;
		margin-bottom: 30px;
		padding-bottom: 5px;
		font-weight: 400;
		font-size: 18px;
		line-height: 1;
		letter-spacing: 0.04px;
		&::before {
			display: inline-block;
			margin-top: -2px;
			margin-right: 8px;
			font-weight: 700;
			font-size: 24px;
			line-height: 1;
			letter-spacing: 0.075px;
			counter-increment: step;
			content: "Шаг " counter(step) ".";
			white-space: nowrap; } }

	&__icon {
		position: relative;
		padding-left: 110px;
		@media (min-width: 320px) and (max-width: 640px) {
			padding-left: 0; } }

	&__icon::after {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 74px;
		height: 74px;
		content: "";
		background-color: #2cd3b8;
		background-position: center;
		background-repeat: no-repeat;
		border-radius: 50%;
		@media (min-width: 320px) and (max-width: 640px) {
			display: none; } }

	&__icon--add-user::after {
		background-image: url("../img/add-user.png");
		background-size: 35px 25px; }
	&__icon--arrow::after {
		background-image: url("../img/arrow.png");
		background-size: 29px 23px; }
	&__icon--cogs::after {
		background-image: url("../img/cogs.png");
		background-size: 36px; }
	&__icon--webin::after {
		background-image: url("../img/webin.png");
		background-size: 40px 26px; }
	&__icon--manager::after {
		background-image: url("../img/manager.png");
		background-size: 32px 37px; }
	&__icon--screen::after {
		background-image: url("../img/screen.png");
		background-size: 32px 29px; } }

.contact {
	padding: 60px 30px 20px;
	&__text {
		margin-bottom: 15px;
		max-width: 650px;
		font-weight: 400;
		font-size: 18px;
		line-height: 1.35;
		letter-spacing: normal;
		color: #393e6b; }
	&__links {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		margin: 70px 0 10px; }
	&__link {
		display: flex;
		max-width: 30%;
		flex-basis: 30%;
		margin-right: 5%;
		font-weight: 400;
		font-size: 18px;
		line-height: 1.35;
		text-decoration: underline;
		color: #0da088;

		&--footer-mail {
			font-weight: 400;
			font-size: 16px;
			line-height: 1.5;
			text-decoration: none;
			letter-spacing: 0.16px;
			color: #ffffff; }
		@media (max-width: 640px) {
			max-width: 100%;
			flex-basis: 100%; }

		@media (min-width: 640px) and (max-width: 1023px) {
			max-width: 45%;
			flex-basis: 45%;
			margin-right: 5%; }

		@media (min-width: 1024px) {
			&:nth-of-type(3n) {
				margin-right: 0; } } }

	&__icon {
		position: relative;
		margin-bottom: 30px;
		padding-top: 6px;
		padding-left: 70px;
		min-height: 55px;
		&::before {
			position: absolute;
			top: 1px;
			left: 0;
			display: block;
			width: 40px;
			height: 40px;
			content: "";
			background-repeat: no-repeat;
			background-position: center;
			background-size: 40px; }
		&--share::before {
			left: 4px;
			background-image: url("../img/link-share.png"); }
		&--arrow::before {
			background-image: url("../img/link-arrow.png"); }
		&--mobile::before {
			background-image: url("../img/link-mobile.png"); }
		&--message::before {
			left: 4px;
			background-image: url("../img/link-message.png"); }
		&--cube::before {
			background-image: url("../img/link-cube.png");
			background-size: 37px 40px; }
		&--mail::before {
			background-image: url("../img/link-mail.png"); }
		&--bot::before {
			left: 4px;
			background-image: url("../img/link-bot.png"); } } }


