@charset "UTF-8";
.icon-facebook { width: 0.53em; height: 1em; fill: initial; }

.icon-menu-button { width: 1em; height: 1em; fill: initial; }

.icon-facebook { width: 0.53em; height: 1em; fill: initial; }

.icon-menu-button { width: 1em; height: 1em; fill: initial; }

body { font-weight: 400; font-size: 16px; line-height: 1.5; font-family: 'Ubuntu', sans-serif; letter-spacing: 0.4px; color: #74757b; }

.page__wrap { position: relative; display: flex; flex-direction: column; margin: 0; padding: 0; overflow: hidden; }

.container { max-width: 1024px; margin: 0 auto; }

.visually-hidden { position: absolute; padding: 0; width: 1px; height: 1px; border: 0; overflow: hidden; clip: rect(1px, 1px, 1px, 1px); }

.button { display: inline-flex; align-items: center; justify-content: center; padding: 15px 25px; vertical-align: middle; min-height: 48px; min-width: 255px; font-weight: 700; font-size: 14px; line-height: 1; font-family: 'Ubuntu', sans-serif; text-decoration: none; text-transform: uppercase; color: #393e6b; border: 1px solid #393e6b; border-radius: 24px; background-color: transparent; transition: all ease-out 0.3s; }

.button:focus, .button:hover { opacity: 0.75; }

.button:active { opacity: 0.35; }

.button--green { color: #ffffff; background-color: #2cd3b8; border: 0; }

.button--blue { color: #ffffff; background-color: #3b4269; border: 0; }

@media (max-width: 420px) and (orientation: portrait) { .button { max-width: 100%; width: 100%; } }

.link { transition: all ease-out 0.3s; }

.link:focus, .link:hover { opacity: 0.75; }

.link:active { opacity: 0.35; }

.logo__link { display: flex; line-height: 1; text-decoration: none; }

.logo__link[href]:focus, .logo__link[href]:hover { opacity: 0.75; }

.logo__link[href]:active { opacity: 0.35; }

.logo__pic { display: block; line-height: 1; }

.header { position: relative; z-index: 100; background-color: #ffffff; box-shadow: 0 3px 7px rgba(0, 0, 2, 0.25); }

.header--fixed { position: fixed; top: 0; right: 0; left: 0; }

.header__nav { max-width: 1600px; margin: 0 auto; display: flex; flex-direction: row; flex-wrap: wrap; align-items: baseline; }

@media (max-width: 1023px) { .header__nav { justify-content: center; } }

.header__logo { padding: 15px 30px; }

@media (min-width: 1023px) and (max-width: 1200px) { .header__logo { display: none; } }

.header__menu { flex-grow: 1; }

@media (max-width: 1023px) { .header__menu { flex-grow: 0; } }

.menu__button { display: none; width: 50px; height: 50px; background-color: transparent; background-image: url("../img/menu.svg"); background-position: center; background-repeat: no-repeat; background-size: 40px; }

@media (max-width: 1023px) { .menu__button { position: absolute; top: 4px; right: 4px; z-index: 200; display: block; } }

.menu__button--closed { background-image: url("../img/closed.svg"); }

.menu__list { display: flex; flex-direction: row; flex-wrap: wrap; list-style: none; }

@media (max-width: 1023px) { .menu__list { padding: 30px 0; flex-direction: column; } }

.menu__link { display: flex; padding: 15px; font-weight: 400; font-size: 18px; line-height: 1.1; letter-spacing: 0.9px; text-decoration: none; text-transform: uppercase; color: #393e6b; }

.menu__link:focus, .menu__link:hover { opacity: 0.75; }

.menu__link:active { opacity: 0.35; }

@media (max-width: 1023px) { .menu__link { padding: 15px 30px; } }

.menu__user { margin-right: auto; }

.menu__wrap { padding: 25px 15px; display: flex; flex-direction: row; flex-wrap: wrap; flex-grow: 1; background-color: #ffffff; }

@media (max-width: 1023px) { .menu__wrap { position: absolute; top: 0; left: -100%; display: flex; width: 100%; flex-direction: column-reverse; justify-content: center; min-height: 100vh; } }

@media (max-width: 1023px) and (orientation: landscape) { .menu__wrap { flex-direction: row-reverse; } }

.menu__dropdown { transition: left ease-out .75s; }

.menu__dropdown--active { left: 0; }

.copyright { display: block; font-weight: 400; font-size: 14px; line-height: 1.25; }

.footer { padding: 35px 15px 45px; color: #b6b9c9; background-color: #393e6b; }

.footer__wrap { max-width: 1600px; margin: 0 auto; display: flex; flex-direction: row; flex-wrap: wrap; }

.footer__col { max-width: 20%; flex-basis: 20%; padding: 15px 15px 0; }

@media (max-width: 1023px) { .footer__col { max-width: 100%; flex-basis: 100%; } }

.footer__telephone { max-width: 55%; flex-basis: 55%; flex-grow: 1; padding-left: 50px; }

@media (max-width: 1023px) { .footer__telephone { display: none; } }

.footer__info { max-width: 25%; flex-basis: 25%; padding-left: 45px; margin-left: auto; flex-grow: 1; }

@media (max-width: 1023px) { .footer__info { display: none; } }

.footer__logo { padding: 5px 15px; margin-bottom: 15px; }

.footer__copyright { padding: 5px 15px 15px; }

.footer__link { color: #ffffff; }

.footer__week { display: block; margin: 8px 0 12px; letter-spacing: 0.16px; }

.telephone__link { display: block; padding: 10px 0; font-weight: 400; font-size: 22px; line-height: 1; letter-spacing: 0.22px; text-decoration: none; color: #ffffff; }

.telephone__link--big { font-size: 26px; line-height: 1; }

.telephone__icon { position: relative; padding-left: 40px; min-height: 30px; }

.telephone__icon::before { position: absolute; top: 9px; left: 1px; display: block; width: 30px; height: 30px; content: ""; background-image: url("../img/phone.png"); background-repeat: no-repeat; background-position: center; background-size: 100%; }

.telephone__desc { margin-top: -5px; margin-bottom: 10px; display: block; font-weight: 400; font-size: 16px; line-height: 1.5; letter-spacing: 0.22px; }

.social__icon { display: block; width: 30px; height: 30px; margin-right: 10px; background-repeat: no-repeat; background-position: center; }

.social__icon--vk { background-image: url("../img/social-vk.png"); background-size: 24px 14px; background-position: left 0px center; }

.social__icon--fb { background-image: url("../img/social-fb.png"); background-size: 11px 21px; background-position: left 7px center; }

.social__icon--tw { background-image: url("../img/social-tw.png"); background-size: 22px 19px; background-position: left 0px center; }

.social__icon--skype { background-image: url("../img/social-skype.png"); background-size: 20px; background-position: left 2px center; }

.socials { display: flex; flex-direction: row; flex-wrap: wrap; justify-content: flex-start; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

.title { display: block; font-weight: 700; font-size: 30px; line-height: 1.25; letter-spacing: 0.75px; text-transform: uppercase; color: #393e6b; }

.title--big { font-size: 36px; line-height: 1.1; letter-spacing: 0.9px; }

.text { font-weight: 400; font-size: 16px; line-height: 1.5; letter-spacing: 0.4px; color: #74757b; }

.banner { padding: 175px 30px 48px; }

.banner--gray { background-color: #eff4f4; }

.banner__title { margin-bottom: 32px; }

.banner__text { max-width: 650px; margin-bottom: 55px; }

.banner__button { margin-right: 20px; margin-bottom: 20px; }

.easy-setup { padding: 67px 30px 17px; }

.easy-setup__title { margin-bottom: 16px; }

.easy-setup__text { margin-bottom: 15px; }

.easy-setup__items { display: flex; flex-direction: row; flex-wrap: wrap; counter-reset: item; margin: 45px 0; }

.easy-setup__item { position: relative; max-width: 25%; flex-basis: 25%; margin-bottom: 22px; }

@media (max-width: 800px) and (orientation: portrait) { .easy-setup__item { margin-bottom: 35px; } }

.easy-setup__item::before { position: absolute; top: 10px; left: -50px; font-weight: 700; font-size: 150px; line-height: 1; color: #e1e2e9; counter-increment: item; content: counter(item); }

@media (max-width: 800px) and (orientation: portrait) { .easy-setup__item::before { top: -15%; left: 35%; } }

@media (max-width: 800px) and (orientation: landscape) { .easy-setup__item::before { top: -15%; left: -11%; } }

@media (max-width: 800px) and (orientation: portrait) { .easy-setup__item:nth-of-type(odd)::before { left: 65%; } }

@media (max-width: 420px) { .easy-setup__item { max-width: 100%; flex-basis: 100%; } }

@media (min-width: 420px) and (max-width: 800px) { .easy-setup__item { max-width: 50%; flex-basis: 50%; } }

.easy-setup__link { font-weight: 400; font-size: 18px; line-height: 1.5; text-decoration: underline; letter-spacing: 0.45px; color: #0da088; }

.easy-setup__icon { position: relative; display: flex; flex-direction: column; }

.easy-setup__icon::before { margin-bottom: 4px; width: 100%; height: 160px; content: ""; background-size: 50px; background-repeat: no-repeat; background-position: left 0 top 77px; }

@media (max-width: 800px) { .easy-setup__icon::before { height: 80px; background-position: left 0 center; } }

.easy-setup__icon--notepad::before { background-image: url("../img/notepad.png"); }

.easy-setup__icon--window::before { background-image: url("../img/window.png"); }

.faq-section { padding: 80px 30px 20px; }

.faq-section--gray { background-color: #eff4f4; }

.faq-section__title { margin-bottom: 25px; }

.faq-section__faq { margin: 62px 0 22px; }

.section { padding: 80px 30px 20px; }

.section--gray { background-color: #eff4f4; }

.section__text { margin-bottom: 15px; max-width: 768px; }

.section__banner { padding-top: 175px; }

.section__buttons { margin-top: 55px; margin-bottom: 45px; }

.section__buttons > * { margin-right: 20px; }

.faq__list { display: flex; flex-direction: row; flex-wrap: wrap; margin: 0; padding: 0; list-style: none; }

.faq__item { position: relative; max-width: 815px; width: 100%; padding-left: 40px; }

.faq__item::before { position: absolute; top: 14px; left: 1px; display: block; width: 8px; height: 8px; content: ""; background-color: #3b4269; border-radius: 50%; }

.faq__title { display: block; margin-bottom: 10px; font-weight: 400; font-size: 24px; line-height: 1.5; letter-spacing: 0; text-decoration: underline; color: #0da088; }

.faq__desc { margin: 10px 0 45px; font-weight: 400; font-size: 16px; line-height: 1.25; letter-spacing: 0.4px; color: #74757b; }

.finish-setup { padding: 65px 30px 50px; }

.finish-setup--gray { background-color: #eff4f4; }

.finish-setup__title { margin-bottom: 17px; }

.finish-setup__text { margin-bottom: 15px; }

.finish-setup__steps { margin: 60px 0 75px; padding: 0; display: block; list-style: none; counter-reset: step; }

.finish-setup__button { min-width: 266px; margin-right: 20px; margin-bottom: 20px; }

.finish-setup__step { display: flex; align-items: center; min-height: 75px; margin-bottom: 30px; padding-bottom: 5px; font-weight: 400; font-size: 18px; line-height: 1; letter-spacing: 0.04px; }

.finish-setup__step::before { display: inline-block; margin-top: -2px; margin-right: 8px; font-weight: 700; font-size: 24px; line-height: 1; letter-spacing: 0.075px; counter-increment: step; content: "Шаг " counter(step) "."; white-space: nowrap; }

.finish-setup__icon { position: relative; padding-left: 110px; }

@media (min-width: 320px) and (max-width: 640px) { .finish-setup__icon { padding-left: 0; } }

.finish-setup__icon::after { position: absolute; top: 0; left: 0; display: block; width: 74px; height: 74px; content: ""; background-color: #2cd3b8; background-position: center; background-repeat: no-repeat; border-radius: 50%; }

@media (min-width: 320px) and (max-width: 640px) { .finish-setup__icon::after { display: none; } }

.finish-setup__icon--add-user::after { background-image: url("../img/add-user.png"); background-size: 35px 25px; }

.finish-setup__icon--arrow::after { background-image: url("../img/arrow.png"); background-size: 29px 23px; }

.finish-setup__icon--cogs::after { background-image: url("../img/cogs.png"); background-size: 36px; }

.finish-setup__icon--webin::after { background-image: url("../img/webin.png"); background-size: 40px 26px; }

.finish-setup__icon--manager::after { background-image: url("../img/manager.png"); background-size: 32px 37px; }

.finish-setup__icon--screen::after { background-image: url("../img/screen.png"); background-size: 32px 29px; }

.contact { padding: 60px 30px 20px; }

.contact__text { margin-bottom: 15px; max-width: 650px; font-weight: 400; font-size: 18px; line-height: 1.35; letter-spacing: normal; color: #393e6b; }

.contact__links { display: flex; flex-direction: row; flex-wrap: wrap; margin: 70px 0 10px; }

.contact__link { display: flex; max-width: 30%; flex-basis: 30%; margin-right: 5%; font-weight: 400; font-size: 18px; line-height: 1.35; text-decoration: underline; color: #0da088; }

.contact__link--footer-mail { font-weight: 400; font-size: 16px; line-height: 1.5; text-decoration: none; letter-spacing: 0.16px; color: #ffffff; }

@media (max-width: 640px) { .contact__link { max-width: 100%; flex-basis: 100%; } }

@media (min-width: 640px) and (max-width: 1023px) { .contact__link { max-width: 45%; flex-basis: 45%; margin-right: 5%; } }

@media (min-width: 1024px) { .contact__link:nth-of-type(3n) { margin-right: 0; } }

.contact__icon { position: relative; margin-bottom: 30px; padding-top: 6px; padding-left: 70px; min-height: 55px; }

.contact__icon::before { position: absolute; top: 1px; left: 0; display: block; width: 40px; height: 40px; content: ""; background-repeat: no-repeat; background-position: center; background-size: 40px; }

.contact__icon--share::before { left: 4px; background-image: url("../img/link-share.png"); }

.contact__icon--arrow::before { background-image: url("../img/link-arrow.png"); }

.contact__icon--mobile::before { background-image: url("../img/link-mobile.png"); }

.contact__icon--message::before { left: 4px; background-image: url("../img/link-message.png"); }

.contact__icon--cube::before { background-image: url("../img/link-cube.png"); background-size: 37px 40px; }

.contact__icon--mail::before { background-image: url("../img/link-mail.png"); }

.contact__icon--bot::before { left: 4px; background-image: url("../img/link-bot.png"); }
